function Writer({ name, associtation }) {
  return (
    <div className="writer-item">
      <div className="writer-name">{name}</div>
      <div className="writer-associtation">{associtation}</div>
    </div>
  );
}

function ReferenceLink({ icon, text, link = "" }) {
  return (
    <a className="link-item github" href={link}>
      <img className="link-icon" src={icon} />
      <div className="link-text">{text}</div>
    </a>
  );
}

