import { Carousel, Typography, Table, Image} from "antd";

import _ from "lodash";

const { Title, Paragraph, Text, Link} = Typography;

const allFiles = ((ctx) => {
    let keys = ctx.keys();
    let values = keys.map(ctx);
    return keys.reduce((o, k, i) => {
      o[k] = values[i];
      return o;
    }, {});
  })(require.context("./examples", true, /.*/));
  
const MODELS = ["clip", "beit", "argus", "dino"];

function getImagePath(set, path) {
    const key = `./${set}/${path.split("/").slice(-2).join("/")}`;
    const result = allFiles[key];
    return result;
  }

function ExampleItem({ set, itemData, sortAscending = true }) {
    let data = MODELS.map((curr, idx) => ({
      model: curr,
      rank: itemData[`rank_${curr}`],
      sim: itemData[`sim_${curr}`],
      ...itemData,
      key: idx,
    }));
  
    data = _.sortBy(data, (x) => (sortAscending ? x["rank"] : -x["rank"]));
  
    const columns = [
      {
        title: "Model",
        dataIndex: "model",
        key: "model",
      },
      {
        title: "Rank",
        dataIndex: "rank",
        key: "rank",
      },
      {
        title: "Score",
        dataIndex: "sim",
        key: "sim",
      },
      {
        title: "Label",
        dataIndex: "value",
        key: "value",
      },
    ];
  
    return (
      <div className="example_item">
        <div className="example_images">
          <div className="example_image">
            Query
            <Image src={getImagePath(set, itemData["query"])} />
          </div>
          <div className="example_image">
            Candidate
            <Image src={getImagePath(set, itemData["gallery"])} />
          </div>
        </div>
        <Table
          dataSource={data}
          columns={columns}
          pagination={false}
          size="small"
          tableLayout="unset"
        />
      </div>
    );
  }
  

export default function({set, title, description, truePoses, trueNegs, falseNegs, falsePoses}) {
    return (
        <section id={`examples_${set}`} className="container examples">
        <h3>{title}</h3>
        <div className="examples_description">
          {description}
        </div>
        <div className="examples_group">
          <h4>Positives</h4>
          <p>Examples that were selected by the EDS Models to have high visual similarity and confirmed by experts.</p>
          <Carousel className="example_items" autoplay>
            {truePoses.map((curr, idx) => (
              <ExampleItem set={set} itemData={curr} sortAscending key={idx} />
            ))}
          </Carousel>
        </div>
        <div className="examples_group">
          <h4>Negatives</h4>
          <p>Examples that were selected by the EDS Models to have high visual similarity but were annotated by experts to be negative.</p>
          <Carousel className="example_items" autoplay>
            {trueNegs.map((curr, idx) => (
              <ExampleItem set={set} itemData={curr} key={idx} />
            ))}
          </Carousel>
        </div>
        <div className="examples_group">
          <h4>Hard Positives</h4>
          <p>Examples that were annotated as positives, while some of the EDS models did not detect high similarity.</p>
          <Carousel className="example_items" autoplay>
            {falseNegs.map((curr, idx) => (
              <ExampleItem set={set} itemData={curr} sortAscending key={idx} />
            ))}
          </Carousel>
        </div>
        <div className="examples_group">
          <h4>Hard Negatives</h4>
          <p>Examples that were annotated as negatives, while some of the EDS models detected high similarity.</p>
          <Carousel className="example_items" autoplay>
            {falsePoses.map((curr, idx) => (
              <ExampleItem set={set} itemData={curr} sortAscending={false} key={idx}/>
            ))}
          </Carousel>
        </div>
      </section>
    )
}